/**
 * showdoc
 * @catalog JS/DataFilter
 * @title 
 * @description js, this
 * @method static
 * @url import { dataFilterByExpr } from '@/funclib/DataFilter.js'
 * @param datas  [{},{}] ,
 * @param expr js 
 * @return [{'f1':'v1','f2':'v2'}] 
 * @remark 
 *  dataFilterByExpr(
 *    [{f1:1},{f1:2},{f1:3},{f1:4}],
 *    "this.f1>2"
 *  )
 *  ==>
 *  [{f1:3},{f1:4}]
 */
export function dataFilterByExpr(datas, expr) {  
  if (expr) {
    let filterDatas = [];
    var execFilterExper = function (expr) {
      return eval(expr)
    };
    for (var i = 0, l = datas.length; i < l; i++) {
      let recd = datas[i];
      if (execFilterExper.call(recd, expr)) {
        filterDatas.push(recd);
      }
    }
    return filterDatas;
  } else {
    return datas;
  }
}



