<template>
  <svg :id="renderTo" />
</template>

<script>
/*
JsBarcode("#imgcode", "123", {
    format: "CODE39", // pharmacode
    width: 3, //
    height: 100, //
    displayValue: true, //
    text: "456", //
    fontOptions: "bold italic", //
    font: "fantasy", //
    textAlign: "left", //
    textPosition: "top", //
    textMargin: 5, //
    fontSize: 15, //
    background: "#eee", //
    lineColor: "#2196f3", //
    margin: 15 //
});
*/
import JsBarcode from "jsbarcode";

export default {
  name: "BarCode",
  props: {
    value: {
      type: [String, Number],
      required: true,
      default: ""
    },
    params: {
      type: Object,
      required: true,
      default: function() {
        return {};
      }
    }
  },
  computed: {
    renderTo() {      
      return this.params.id || "barcode1";
    }
  },
  data() {
    return {};
  },
  mounted() {
    let p = {
      width: 1,
      height: 60,
      fontSize:12,
      margin:0
    };
    Object.assign(p, this.params);
    JsBarcode("#" + this.renderTo, this.value, p);
  }
};
</script>

<style lang="scss" scoped>
</style>


