<template>
  <div :style="params.style">
    <canvas v-if="type==='yuan'" ref="gongzhangcanvas" width="160" height="160"></canvas>
    <canvas v-else ref="gongzhangcanvas" width="165" height="165"></canvas>
  </div>
</template>

<script>
export default {
  name: "GongZhang",
  props: {
    params: {
      type: Object,
      required: true,
      default: function() {
        return {};
      }
    }
  },
  computed: {
    cnName() {
      return this.params.cnName;
    },
    enName() {
      return this.params.enName;
    },
    zhangName() {
      return this.params.zhangName || "";
    },
    type() {
      return this.params.type || "yuan";
    }
  },
  methods: {
    // 
    createGongZhang() {      
      let canvas = this.$refs.gongzhangcanvas;
      if (this.enName) {
        if (this.type === "yuan") {
          this.createEnGongZhang(
            canvas,
            this.cnName,
            this.enName,
            this.zhangName
          );
        } else {
          this.createEnGongZhang2(
            canvas,
            this.cnName,
            this.enName,
            this.zhangName
          );
        }
      } else {
        if (this.type === "yuan") {
          this.createCnGongZhang(canvas, this.cnName, this.zhangName);
        } else {
          this.createCnGongZhang2(canvas, this.cnName, this.zhangName);
        }
      }
    },
    // 
    createCnGongZhang(canvas, company, zhangName) {
      var context = canvas.getContext("2d");

      // 
      var width = canvas.width / 2;
      var height = canvas.height / 2;
      context.lineWidth = 2;
      context.strokeStyle = "#f00";
      context.beginPath();
      context.arc(width, height, 78, 0, Math.PI * 2);
      context.stroke();
      context.save();

      context.lineWidth = 1;
      context.strokeStyle = "#f00";
      context.beginPath();
      context.arc(width, height, 75, 0, Math.PI * 2);
      context.stroke();
      context.save();

      //
      this.create5star(context, width, height, 25, "#f00", 0);

      // 
      context.font = "18px Helvetica";
      context.textBaseline = "middle"; //
      context.textAlign = "center"; //
      context.lineWidth = 1;
      context.fillStyle = "#f00";
      context.fillText(zhangName, width, height + 53);

      // 
      context.translate(width, height); // ,
      context.font = "20px Helvetica";
      var count = company.length; // 
      var angle = (4 * Math.PI) / (3 * (count - 1)); // 
      var chars = company.split("");
      var c;
      for (var i = 0; i < count; i++) {
        c = chars[i]; // 
        if (i == 0) context.rotate((5 * Math.PI) / 6);
        else context.rotate(angle);
        context.save();
        context.translate(64, 0); // ,x
        context.rotate(Math.PI / 2); // 90,x
        context.fillText(c, 0, 5); // 
        context.restore();
      }
    },
    // 
    createCnGongZhang2(canvas, company, zhangName) {
      var context = canvas.getContext("2d");    
      context.strokeStyle = "red"; //
      context.textBaseline = "middle"; //
      context.textAlign = "center"; //
      context.lineWidth = 2; //1
      //3    
      this.createBezierEllipse(context, 85, 79, 79, 55); //1
      context.lineWidth = 1;
      this.createBezierEllipse(context, 85, 79, 76, 52); //2

      // 
      context.font = "bolder 15px SimSun";
      context.lineWidth = 1;
      context.fillStyle = "#f00";
      context.fillText(zhangName, canvas.width / 2 + 3, canvas.height / 2 + 10);
      context.save();

      //
      var ccircle = {
        x: canvas.width / 2,
        y: canvas.height / 2,
        radius: 59
      };
      var cstartAngle = 170; //
      var cendAngle = 15; //
      var cradius = ccircle.radius; //
      var cangleDecrement = (cstartAngle - cendAngle) / (company.length - 1); //
      context.font = "12px SimSun";
      var cratioX = 66 / ccircle.radius; //
      var cratioY = 57 / ccircle.radius; //
      //
      context.scale(cratioX, cratioY);
      var cindex = 0;
      for (var cindex = 0; cindex < company.length; cindex++) {
        context.save();
        context.beginPath();
        //
        context.translate(
          ccircle.x + Math.cos((Math.PI / 180) * cstartAngle) * cradius - 6,
          ccircle.y - Math.sin((Math.PI / 180) * cstartAngle) * cradius + 14
        );
        context.rotate(Math.PI / 2 - (Math.PI / 180) * cstartAngle); //Math.PI/290  Math.PI/180*X
        context.fillText(company.charAt(cindex), 0, 0);
        context.strokeText(company.charAt(cindex), 0, 0);
        cstartAngle -= cangleDecrement;
        context.restore();
      }
    },
    // 
    createEnGongZhang(canvas, company, companyEn, zhangName) {
      var context = canvas.getContext("2d");
      context.translate(0, 0); //
      // 
      var width = canvas.width / 2;
      var height = canvas.height / 2;
      //1
      context.lineWidth = 2;
      context.strokeStyle = "#f00";
      context.beginPath();
      context.arc(width, height, 78, 0, Math.PI * 2);
      context.stroke();
      context.save();

      //2
      context.lineWidth = 1;
      context.strokeStyle = "#f00";
      context.beginPath();
      context.arc(width, height, 63, 0, Math.PI * 2);
      context.stroke();
      context.save();

      //
      this.create5star(context, width, height, 20, "#f00", 0);

      // 
      context.font = "bolder 15px SimSun";
      context.textBaseline = "middle"; //
      context.textAlign = "center"; //
      context.lineWidth = 1;
      context.fillStyle = "#f00";
      context.fillText(zhangName, width, height + 50);

      // 
      context.translate(width, height); // ,
      context.font = "bolder 18px SimSun";
      var count = company.length; // 
      var angle = (4 * Math.PI) / (3 * (count - 1)); // 
      var chars = company.split("");
      var c;
      for (var i = 0; i < count; i++) {
        c = chars[i]; // 
        if (i == 0) context.rotate((5 * Math.PI) / 6);
        else context.rotate(angle);
        context.save();
        // ,x
        context.translate(52, 0);
        context.rotate(Math.PI / 2); // 90,x
        context.fillText(c, 0, 5); // 
        context.restore();
      }
      //
      context.translate(width - 80, height - 80); // ,
      context.font = "bolder 10px SimSun";
      var ecount = companyEn.length; // 
      var eangle = (5 * Math.PI) / (3 * ecount); // 
      var echars = companyEn.split("");
      var ec;
      for (var j = 0; j < ecount; j++) {
        ec = echars[j]; // 
        if (j == 0) context.rotate((6 * Math.PI) / 7 - 1);
        else context.rotate(eangle);
        context.save();
        // ,x
        context.translate(74, 0);
        context.rotate(Math.PI / 2); // 90,x
        context.fillText(ec, 0, 4.8); // 
        context.restore();
      }
    },
    // 
    createEnGongZhang2(canvas, company, companyEn, zhangName) {
      var context = canvas.getContext("2d");
      context.strokeStyle = "red"; //
      context.textBaseline = "middle"; //
      context.textAlign = "center"; //
      context.lineWidth = 2; //1
      //3    
      this.createBezierEllipse(context, 85, 79, 79, 55); //1
      context.lineWidth = 1;
      this.createBezierEllipse(context, 85, 79, 76, 52); //2
      context.lineWidth = 2;
      this.createBezierEllipse(context, 85, 79, 63, 39); //3
      // 
      context.font = "bolder 10px SimSun"; // 
      context.fillStyle = "red"; //
      context.fillText(zhangName, canvas.width / 2 + 3, canvas.height / 2 + 25);
      context.save(); //
      //
      var circle = {
        x: canvas.width / 2,
        y: canvas.height / 2,
        radius: 58
      };
      var startAngle = 220; //
      var endAngle = -40; //
      var radius = circle.radius; //
      var angleDecrement = (startAngle - endAngle) / (companyEn.length - 1); //
      context.font = "bolder 10px SimSun";
      context.lineWidth = 1; //
      var ratioX = 70 / circle.radius; //
      var ratioY = 45 / circle.radius; //
      // 
      context.scale(ratioX, ratioY);
      var index = 0;
      for (var index = 0; index < companyEn.length; index++) {
        //
        context.save();
        context.beginPath();
        context.translate(
          circle.x + Math.cos((Math.PI / 180) * startAngle) * radius - 12,
          circle.y - Math.sin((Math.PI / 180) * startAngle) * radius + 19
        ); // +-
        context.rotate(Math.PI / 2 - (Math.PI / 180) * startAngle); //Math.PI/290  Math.PI/180*X
        context.fillText(companyEn.charAt(index), 0, 0);
        context.strokeText(companyEn.charAt(index), 0, 0);
        startAngle -= angleDecrement;
        context.restore();
      }

      // 
      context.font = "bolder 14px SimSun";
      context.lineWidth = 1;
      context.fillStyle = "#f00";
      context.fillText(
        company.substring(0, 6),
        canvas.width / 2 - 11,
        canvas.height / 2 + 6
      );
      context.save();
      context.font = "bolder 14px SimSun";
      context.lineWidth = 1;
      context.fillStyle = "#f00";
      context.fillText(
        company.substring(6, 12),
        canvas.width / 2 - 12,
        canvas.height / 2 + 25
      );
      context.save();
      context.font = "bolder 14px SimSun";
      context.lineWidth = 1;
      context.fillStyle = "#f00";
      context.fillText(
        company.substring(12, company.length),
        canvas.width / 2 - 12,
        canvas.height / 2 + 40
      );
      context.save();
    },
    // 5
    create5star(context, sx, sy, radius, color, rotato) {
      context.save();
      context.fillStyle = color;
      context.translate(sx, sy); //
      context.rotate(Math.PI + rotato); //
      context.beginPath(); //
      var x = Math.sin(0);
      var y = Math.cos(0);
      var dig = (Math.PI / 5) * 4;
      for (var i = 0; i < 5; i++) {
        //
        var x = Math.sin(i * dig);
        var y = Math.cos(i * dig);
        context.lineTo(x * radius, y * radius);
      }
      context.closePath();
      context.stroke();
      context.fill();
      context.restore();
    },
    // 
    createBezierEllipse(ctx, x, y, a, b) {
      var k = 0.5522848,
        ox = a * k, // 
        oy = b * k; // </p> <p>
      ctx.beginPath();
      //
      ctx.moveTo(x - a, y);
      ctx.bezierCurveTo(x - a, y - oy, x - ox, y - b, x, y - b);
      ctx.bezierCurveTo(x + ox, y - b, x + a, y - oy, x + a, y);
      ctx.bezierCurveTo(x + a, y + oy, x + ox, y + b, x, y + b);
      ctx.bezierCurveTo(x - ox, y + b, x - a, y + oy, x - a, y);
      ctx.closePath();
      ctx.stroke();
    }
  },
  mounted() {
    this.createGongZhang();
  }
};
</script>


